import React from 'react';

import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { FormFieldProps } from './form-field.interface';

export const FormField: React.FC<FormFieldProps> = ({ messageId, children, state = 'default', message, ...props }) => {
  return (
    <fieldset
      className={clsx('zep-inline-flex', 'zep-flex-col', 'zep-flex-nowrap')}
      data-testid="zep-form-fieldset"
      {...props}
    >
      {children}
      {message && (
        <span
          id={messageId}
          className={clsx(
            {
              'zep-text-error': state === 'error',
              'zep-text-info': state === 'info',
              'zep-text-typography-dark-70': state === 'default',
            },
            'zep-mt-0.5 zep-flex zep-flex-row zep-items-center zep-gap-0.5 zep-text-0.875',
          )}
        >
          {state === 'error' && <FunctionalIcon name="warning-triangle-filled" className="zep-text-error" />}
          {state === 'info' && <FunctionalIcon name="info-circle-filled" className="zep-text-info" />}
          <span className='zep-mt-[2px]'>{message}</span>
        </span>
      )}
    </fieldset>
  );
};
